.wave-background {
  width: 100%;
  height: 700px;
  border-radius: 5px;
  position: relative;
  overflow: hidden;
}

.wave {
  opacity: .7;
  position: absolute;
  background: #3867d6;
  width: 4500px;
  height: 4500px;
  margin-left: -1350px;
  margin-top: -3950px;
  transform-origin: 50% 50%;
  border-radius: 43%;
  animation: drift 3000ms infinite linear;
}

.wave.-three {
  animation: drift 5000ms infinite linear;
}

.wave.-two {
  animation: drift 7000ms infinite linear;
  opacity: .1;
  background: #3867d6;
}

@keyframes drift {
  from {
    transform: rotate(0deg);
  }
  from {
    transform: rotate(180deg);
  }
}

